<template>
  <v-container px-12 py-6 fluid class="overall-style">
    <v-row>
      <a href="#cases" style="color: white; text-decoration: none; background: #4A148C;">{{ $t("anchor_cases") }}</a>
      <v-spacer></v-spacer>
      <button style="color: white; text-decoration: none; background: #4A148C;" @click="changeLanguage">{{ $t("other_language") }}</button>
    </v-row>
  </v-container>
</template>

<script>
export default {
    methods: {
        changeLanguage() {
            this.$i18n.locale = this.$i18n.locale === "en-US" ? "zh-CN" : "en-US";
            // save language to local storage
            localStorage.setItem("lang", this.$i18n.locale);
        },
    },
}
</script>

