<script>
import MarkdownItVue from "markdown-it-vue";
import "markdown-it-vue/dist/markdown-it-vue.css";
// import GithubButton from 'vue-github-button';

import axios from "axios";

export default {
  components: {
    MarkdownItVue,
    // GithubButton
  },
  data() {
    return {
      listData: [],
      number: 0,
      airtable_number: 0,
      doc_number: 0,
      search: "",
      string_code: "HJjas1SQnwWz8kE1iQQVKixaSVsfk2GdcKzZ_phg",
      expanded: [],
      options: {
        markdownIt: {
          linkify: true,
        },
        linkAttributes: {
          attrs: {
            target: "_blank",
            rel: "noopener",
          },
        },
      },
    };
  },
  computed: {
    headers() {
      if (this.$i18n.locale === "zh-CN") {
        return [
          {
            text: this.$t("table_time"),
            value: "time",
          },
          {
            text: this.$t("table_location"),
            value: "location",
          },
          {
            text: this.$t("table_title"),
            value: "title",
          },
        ];
      } else {
        return [
          {
            text: this.$t("table_time"),
            value: "time",
          },
          {
            text: this.$t("table_location"),
            value: "location_en",
          },
          {
            text: this.$t("table_title"),
            value: "title_en",
          },
        ];
      }
    },
  },

  created() {
    this.getData();
  },
  mounted() {
    let recaptchaScript = document.createElement("script");
    recaptchaScript.setAttribute("src", "https://buttons.github.io/buttons.js");
    document.head.appendChild(recaptchaScript);
  },
  methods: {
    async getData() {
      if (
        localStorage.getItem("listData") &&
        localStorage.getItem("listDataTime") > Date.now() - 1000 * 60 * 1
      ) {
        this.listData = JSON.parse(localStorage.getItem("listData"));
        this.number = +localStorage.getItem("number");
        this.airtable_number = +localStorage.getItem("airtable_number");
        // this.doc_number = +localStorage.getItem("doc_number");
        // console.log("loading local data stored.");
        return;
      }
      // console.log("getting data from server.");
      const response = await axios.get(
        "https://api.github.com/repos/lockdemic/Lockdemic.ICU/issues?state=closed",
        {
          headers: {
            Authorization:
              "token " + this.string_code.split("").reverse().join(""),
          },
        }
      );
      let data = response.data;
      // console.log(data);
      let ret = [];
      for (let i = 0; i < data.length; i++) {
        let item = data[i];

        // let obj = {};
        let body = item.body.toString();
        if (item.title == "Other data sources") {
          try {
            let body_list = body.split("\r\n");
            this.airtable_number = +body_list[0];
            this.doc_number = +body_list[2];
          } catch (e) {
            console.log(e);
          }
          continue;
        }
        let ind = body.indexOf("**********");
        // console.log(item)
        if (ind > 0) {
          // body = "{" + body.substring(0, ind).replaceAll("\r\n", "") + "}";
          body = body.substring(0, ind);
          let body_list = body.split("\r\n");
          // console.log(body.indexOf("\n"));
          let obj = {};
          try {
            obj["id"] = item.id;
            for (let i = 0; i < body_list.length; i++) {
              // console.log(body_list[i]);
              let colon_ind = body_list[i].indexOf(":");
              if (colon_ind < 0) {
                continue;
              }
              let key = body_list[i].substring(0, colon_ind).trim();
              let value = body_list[i].substring(colon_ind + 1).trim();
              if (key == "death_number") {
                this.number += +value;
              }
              obj[key] = value;
            }
            // let obj = JSON.parse(body);
            // console.log(obj, Object.keys(obj).length !== 0);
            if (!Object.keys(obj).length !== 0) {
              ret.push(obj);
            }
          } catch {
            // console.log(e);
          }
        }
      }
      this.listData = ret;
      localStorage.setItem("listData", JSON.stringify(ret));
      localStorage.setItem("listDataTime", Date.now());
      localStorage.setItem("number", this.number);
      localStorage.setItem("airtable_number", this.airtable_number);
      // localStorage.setItem("doc_number", this.doc_number);
    },
  },
};
</script>

